<template>
  <div>
    <Modal v-model="showAudit" :footerHide="true" title="报名用户列表" style="text-align: center" width="700px">
      <Table stripe :columns="columns" :data="data"></Table>
      <div class="pagesWrapper">
        <Page :total="count" show-total @on-change="pageChange" :current="query.page"></Page>
      </div>
    </Modal>
  </div>
</template>

<script>
  import qs from 'qs'
  export default {
    data () {
      return {
        showAudit: false,
        columns: [
          {
            title: 'ID',
            key: 'Id'
          },
          {
            title: '账号名',
            key: 'Name'
          },
          {
            title: '创建时间',
            key: 'CreatedTime'
          },
          {
            title: '操作',
            width: 200,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'success',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.pass(params.row.Id)
                    }
                  }
                }, '通过')
              ])
            }
          }
        ],
        data: [],
        count: 0,
        query: {
          activityID: '',
          page: 1,
          per: 10
        }
      }
    },
    methods: {
      show (id) {
        this.query.activityId = id
        this.showAudit = true
        this.getUserList(activityId)
      },
      getUserList () {
        this.$loading()
        this.$http.post('/api/admin/activity/userlist', qs.stringify(this.query)).then((response) => {
          this.data = response.data.data
          this.count = response.data.total
          this.query.page = response.data.page
          this.query.per = response.data.per
          this.$loadingHide()
        }).catch(() => {
          this.$loadingHide()
          this.$Message.error('通讯失败，请重试')
        })
      },
      pageChange (page) {
        this.$loading()
        this.query.page = page
        this.getUserList()
      },
      pass (id) {
        this.$http.post('/api/admin/activity/pass?activityId=' + this.query.activityId + 'userId=' + id).then((response) => {
          console.log(response)
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .pagesWrapper
    margin-top 20px
    text-align right

</style>



